import { BasicColumn } from '@/components/Table';
import { defHttp } from '@/utils/http/axios';

enum Api {
  addPlanData = '/po/inspection-plan/create',
  getPlanData = '/po/inspection-plan/get',
  getPlanDtlData = '/po/inspection-plan/dtl/list',
  getPlanStationData = '/po/inspection-plan/dtl/station-list',
  getSafeCheckIndexPage = '/baseset/safeCheck/list-byCheckType',
  getStation = '/baseset/common/list-station',
}

//明细
export function getPlanDtlData(params) {
  return defHttp.get({ url: Api.getPlanDtlData, params });
}
//查询
export function getPlanData(params) {
  return defHttp.get({ url: Api.getPlanData, params });
}

// 新增
export function createPlan(data) {
  return defHttp.post({ url: Api.addPlanData, data });
}
export function getSafeCheckIndexPage(data) {
  data.checkType = 'inspection';
  return defHttp.post({ url: Api.getSafeCheckIndexPage, data });
}
export function getPlanStationData(params) {
  return defHttp.get({ url: Api.getPlanStationData, params });
}
export function getStation(data) {
  return defHttp.post({ url: Api.getStation, data });
}

export const dtlColumns: BasicColumn[] = [
  { title: '项目', dataIndex: 'indexItem',align: 'left',headAlign:'center', width: 160 },
  { title: '巡检内容及标准', dataIndex: 'indexContent',align: 'left',headAlign:'center', width: 160 },
];
